* {
  box-sizing: border-box;
}

.grid-container {
  width: 70%;
  display: grid;
  margin: 0 auto;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 10px;
}

.btn {
  display: flex;
  justify-content: center;
  margin: 20px 0;
  list-style: none;
}

.btn button,
.btn li>a {
  border-style: none;
  padding: 5px 10px;
  margin-right: 10px;
  border-radius: 5px;
  background: #30a7d7;
  color: #fff;
  font-size: 1em;
  text-decoration: none;
}

.btn li {
  margin-top: 15px;
}

.btn select {
  font-size: 1em;
  margin-right: 10px;
}

.pagination {
  display: flex;
  flex-wrap: wrap;

}